/*===============================================*/
/* 4. HEADER */
/*===============================================*/
.header {
    inline-size: 100%;
    padding: 10px 0;
}

.header--absolute {
    position: absolute;
    inset-block-start: 0px;
    inset-inline-start: 0px;
    z-index: 888;
}

.header--fixed {
    position: fixed;
    inset-block-start: 0px;
    inset-inline-start: 0px;
    z-index: 888;
}


.header__inner {
    inline-size: calc(100% - 40px);
    padding: 10px 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.header__icon {
    cursor: pointer;
}

.header__icon img {
    display: block;
    max-inline-size: 24px;
}

.header__icon--empty-space {
    inline-size: 24px;
}


.header--page {
    background-color: #ffffff !important;
    box-shadow: 0px 2px 4px rgba(61, 99, 143, 0.1);
}

.header--page a {
    text-decoration: none;
}

.hearder__title {
    font-size: 24px;
}


@keyframes cartnr {
    0% {
        transform: scale(1.5);
    }

    50% {
        transform: scale(0.8);
    }

    100% {
        transform: scale(1.5);
    }
}

.contents {
    padding: 90px 0 100px 0;
}

.round-card {
    border-radius: 30px;
    background-color: #fff;
    border: none !important;
    padding: 10px;
}

.sales {
    font-size: large;
    font-weight: 800;
}

.sales-today {
    font-size: x-large;
    font-weight: 800;
}



/*===============================================*/
/* 5. BOTTOM TOOLBAR */
/*===============================================*/
.bottom-navigation {
    inline-size: 100%;
    position: fixed;
    inset-block-end: 0px;
    z-index: 888;
    padding: 12px 0;
    overflow: hidden;
    background-color: rgb(255, 255, 255);
    border-radius: 30px 30px 0 0;
    box-shadow: 0 2px 2px 0 rgb(0 0 0 / 14%), 0 3px 1px -2px rgb(0 0 0 / 20%), 0 1px 10px 0 rgb(0 0 0 / 12%);
}

.bottom-navigation__icons {
    display: flex;
    align-items: center;
    justify-content: space-between;
    inline-size: calc(100% - 80px);
}


.mdl-chip-tab {
    border-radius: 100px;
    background-color: #dedede;
    display: inline-block;
    color: rgba(0, 0, 0, .87);
    margin: 20px 0;
    margin-inline-start: 16px;
}

.mdl-chip-tab .is-active {
    color: #fff !important;
    background-color: #2B305E;
    border-radius: 100px;
}

